// firebaseUtils.js
import { database, ref, set } from '../firebase';

const addUserSwipe = async (userId, jobId, decision) => {
  try {
    const userSwipeRef = ref(database, `users/${userId}/swipes/${jobId}`);
    await set(userSwipeRef, { decision });
  } catch (error) {
    console.error("Error adding user swipe:", error);
  }
};

export { addUserSwipe };