import { Link } from "react-router-dom";

const MobileNav = ({ isOpen, onToggle, isLoggedIn, onLogout }) => {
  const handleLinkClick = () => {
    onToggle(); // Call the onToggle function to close the menu
  };

  return (
    <div
      className={`fixed top-0 left-0 w-64 h-full bg-gray-900 text-white transform transition-transform z-20 ${
        isOpen ? "translate-x-0" : "-translate-x-full"
      }`}
    >
      <ul className="space-y-2 py-4 px-6">
        <li>
          <Link to="/" onClick={handleLinkClick}>
            Home
          </Link>
        </li>
        {isLoggedIn ? (
          <div>
            <li>
              <Link to="/profile" onClick={handleLinkClick}>
                Profile
              </Link>
            </li>
            <li>
              <Link to="/dashboard" onClick={handleLinkClick}>
                Dashboard
              </Link>
            </li>
            <li>
              <button
                className="bg-transparent hover:bg-white text-white hover:text-gray-900 border border-white hover:border-transparent rounded-md px-4 py-2 transition-colors"
                onClick={() => {
                  handleLinkClick();
                  onLogout();
                }}
              >
                Logout
              </button>
            </li>
          </div>
        ) : (
          <>
            <li>
              <Link to="/login" onClick={handleLinkClick}>
                Login
              </Link>
            </li>
            <li>
              <Link to="/signup" onClick={handleLinkClick}>
                Sign Up
              </Link>
            </li>
          </>
        )}
      </ul>
    </div>
  );
};

export default MobileNav;