import React, { useState } from "react";
import SwipeableViews from "react-swipeable-views";
import JobCard from "./JobCard";

const JobSwipeContainer = ({ handleSwipe, jobData }) => {
  const [swipeIndex, setSwipeIndex] = useState(0);
  const [prevSwipeIndex, setPrevSwipeIndex] = useState(0);

  const determineSwipeDirection = () => {
    // Compare the current swipeIndex with the previous one to determine the direction
    return swipeIndex > prevSwipeIndex ? "right" : "left";
  };

  const handleChangeIndex = (index) => {
    setPrevSwipeIndex(swipeIndex); // Update the previous swipe index
    setSwipeIndex(index);
    const swipeDirection = determineSwipeDirection();
    handleSwipe(index, swipeDirection); // Call the handleSwipe function with the index and swipe direction
  };

  return (
    <div className="job-swipe-container">
      <h1 className="text-3xl font-semibold mb-4">Swipe Through Jobs</h1>
      <SwipeableViews
        enableMouseEvents
        index={swipeIndex}
        onChangeIndex={handleChangeIndex} // Use the custom handleChangeIndex function
      >
        {jobData.map((job) => (
          <div key={job.id} className="relative">
            <JobCard job={job} />
          </div>
        ))}
      </SwipeableViews>
    </div>
  );
};

export default JobSwipeContainer;
