import React, { useState } from "react";

const AppFeatures = () => {
  const [activeTab, setActiveTab] = useState("Getting Hired");

  const handleTabChange = (tab) => {
    setActiveTab(tab);
  };

  const tabContent = {
    "Getting Hired": "Content for Getting Hired",
    "Finding Talent": "Content for Finding Talent",
    Partnering: "Content for Partnering",
  };

  return (
    <div>
      <h2 className="text-2xl font-semibold mb-4">App Features</h2>
      <div className="flex space-x-4">
        {Object.keys(tabContent).map((tab) => (
          <button
            key={tab}
            onClick={() => handleTabChange(tab)}
            className={`${
              activeTab === tab
                ? "bg-blue-500 text-white"
                : "bg-gray-200 text-gray-700"
            } px-4 py-2 rounded-md`}
          >
            {tab}
          </button>
        ))}
      </div>
      <div className="mt-4">
        <p>{tabContent[activeTab]}</p>
      </div>
    </div>
  );
};

export default AppFeatures;