import React, { useState } from "react";

const UserProfile = ({ user }) => {
  const [editing, setEditing] = useState(false);
  const [displayName, setDisplayName] = useState(user.displayName);
  const [email, setEmail] = useState(user.email);

  const handleEditProfile = () => {
    setEditing(true);
  };

  const handleSaveProfile = () => {
    // TODO: Update the user's profile in the database
    // For example, you can use Firebase's updateProfile method
    // to update the user's display name and email.
    // auth.currentUser.updateProfile({
    //   displayName: displayName,
    //   email: email,
    // });
    setEditing(false);
  };

  return (
    <div>
      <h2 className="text-2xl font-semibold mb-4">User Profile</h2>
      <div className="border rounded-lg p-4">
        {editing ? (
          <form>
            <div className="mb-4">
              <label htmlFor="displayName" className="block font-semibold">
                Display Name
              </label>
              <input
                type="text"
                id="displayName"
                value={displayName}
                onChange={(e) => setDisplayName(e.target.value)}
                className="border rounded-md px-3 py-2 w-full"
              />
            </div>
            <div className="mb-4">
              <label htmlFor="email" className="block font-semibold">
                Email
              </label>
              <input
                type="email"
                id="email"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
                className="border rounded-md px-3 py-2 w-full"
              />
            </div>
            <div className="flex space-x-4">
              <button
                type="button"
                className="bg-blue-500 text-white px-4 py-2 rounded-md"
                onClick={handleSaveProfile}
              >
                Save
              </button>
              <button
                type="button"
                className="bg-gray-500 text-white px-4 py-2 rounded-md"
                onClick={() => setEditing(false)}
              >
                Cancel
              </button>
            </div>
          </form>
        ) : (
          <div>
            <p>
              <span className="font-semibold">Display Name:</span>{" "}
              {user.displayName}
            </p>
            <p>
              <span className="font-semibold">Email:</span> {user.email}
            </p>
            <button
              type="button"
              className="bg-blue-500 text-white mt-4 px-4 py-2 rounded-md"
              onClick={handleEditProfile}
            >
              Edit Profile
            </button>
          </div>
        )}
      </div>
    </div>
  );
};

export default UserProfile;
